.card {
    border: 1px solid #f3f3f3;
    padding: 30px;
    width: 210px;
    border-radius: 40px;
    transition: box-shadow 0.2s ease-in-out, transform 0.4s ease-in-out;
    margin: 20px 20px 20px;
    // cursor: pointer;
    .plus {
        cursor: pointer;
    }
    .favorite {
        position: absolute;
        cursor: pointer;
    }
    h5 {
        font-weight: 400;
        font-size: 13px;
        margin: 14px 0;
    }
    span {
        font-size: 13px;
        opacity: 0.5;
        text-transform: uppercase;
    }
    b {
        font-size: 14px;
    }
    &:hover {
        border: 1px solid #F8F8F8;
        box-shadow: 0px 14px 30px rgba(0, 0, 0, 0.06);
        // margin-top: -10px;
        transform: translateY(-5px);
    }
}

